@import url("https://fonts.googleapis.com/css?family=Crimson+Text:400,400i,700,700i|Noto+Sans:400,400i,700,700i");

// Font Family Variables
$sans-serif: "Noto Sans", "Open Sans", sans-serif;
$serif: "Crimson Text", serif;
$heading-font-family: $sans-serif;
$body-font-family: $sans-serif;

// Font Weight Variables
$regular: 400;
$bold: 700;
$body-font-weight: $regular;
$heading-font-weight: $regular;
$button-font-weight: $bold;

// Font Sizes
$base-font-size: 24px;
$mobile-font-size: 15px;
$h1-font-size: 2.441em;
$h2-font-size: 1.953em;
$h3-font-size: 1.563em;
$h4-font-size: 1.25em;
$h5-font-size: 1.2em;
$h6-font-size: 1.1em;

// CSS Classes
.sans-serif {
  font-family: $sans-serif;
}

.serif {
  font-family: $serif;
  font-size: 1.2em;
}
